<template>
  <div ref="form" id="MixingForm" class="form form--mixing">
    <div class="form__messages text-center">
      <b-spinner v-if="show.loading" label="Submitting form"></b-spinner>

      <div v-if="show.failure" class="form__message form__message--failure">
        {{ message.failure }}
      </div>
      <div v-if="show.success" class="form__message form__message--success">
        {{ message.success }}
      </div>
    </div>

    <ValidationObserver>
      <b-form @submit.prevent="onSubmit" v-if="show.form" class="form--small">
        <div class="row">
          <div class="col-12 col-lg-4 mb-4">
            <validation-provider
              rules="required"
              name="name"
              v-slot="{ errors }"
            >
              <b-form-group id="Name" label="Name:" label-for="NameInput">
                <b-form-input
                  id="NameInput"
                  v-model="fields.name"
                  placeholder="Full Name"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
          <div class="col-12 col-lg-4 mb-4">
            <validation-provider
              rules="email|required"
              name="email"
              v-slot="{ errors }"
            >
              <b-form-group id="Email" label="Email:" label-for="EmailInput">
                <b-form-input
                  id="EmailInput"
                  v-model="fields.email"
                  type="email"
                  placeholder="example@gmail.com"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
          <div class="col-12 col-lg-4 mb-4">
            <validation-provider
              rules="required"
              name="artist name"
              v-slot="{ errors }"
            >
              <b-form-group
                id="ArtistName"
                label="Artist Name:"
                label-for="ArtistInput"
              >
                <b-form-input
                  id="ArtistInput"
                  v-model="fields.artist_name"
                  placeholder="Drake"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
        </div>

        <div class="row align-items-end">
          <div class="col-12 col-lg-6 mb-4">
            <validation-provider
              rules="required"
              name="song title"
              v-slot="{ errors }"
            >
              <b-form-group
                id="SongTitle"
                label="Song Title:"
                label-for="TitleInput"
              >
                <b-form-input
                  id="TitleInput"
                  v-model="fields.song_title"
                  placeholder="Passionfruit"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
          <div class="col-6 col-lg-3 mb-4">
            <validation-provider
              rules="required"
              name="song key"
              v-slot="{ errors }"
            >
              <b-form-group id="Key" label="Song Key:" label-for="KeyInput">
                <b-form-input
                  id="KeyInput"
                  v-model="fields.song_key"
                  placeholder="Cm / C minor"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
          <div class="col-6 col-lg-3 mb-4">
            <validation-provider
              rules="required"
              name="artist name"
              v-slot="{ errors }"
            >
              <b-form-group id="BPM" label="BPM:" label-for="BPMInput">
                <b-form-input
                  id="BPMInput"
                  type="number"
                  v-model="fields.song_bpm"
                  placeholder="136"
                  class="text-right"
                  :min="40"
                  :max="200"
                  autocomplete="off"
                  :no-wheel="true"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
        </div>

        <div class="row">
          <div class="col-12 col-lg-6 mb-4">
            <validation-provider
              rules="url"
              name="song files"
              v-slot="{ errors }"
            >
              <b-form-group
                id="Reference"
                label="Reference Song:"
                label-for="ReferenceInput"
              >
                <b-form-input
                  id="ReferenceInput"
                  v-model="fields.song_reference"
                  placeholder="https://open.spotify.com/track/123456a"
                  autocomplete="off"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
          <div class="col-12 col-lg-6 mb-4">
            <validation-provider
              rules="required|url"
              name="song files"
              v-slot="{ errors }"
            >
              <b-form-group
                id="Files"
                label="Song Files:"
                label-for="FilesInput"
              >
                <b-form-input
                  id="FilesInput"
                  v-model="fields.song_files"
                  placeholder="https://wetransfer.com/downloads/123456a"
                  autocomplete="off"
                  required
                ></b-form-input>
              </b-form-group>
              <span class="form-error form-error--narrow">{{ errors[0] }}</span>
            </validation-provider>
          </div>
        </div>

        <!-- Honeypot -->
        <input
          v-model="honeypot"
          type="text"
          name="subject"
          class="sr-only"
          tabindex="-1"
          autocomplete="off"
          aria-hidden
        />

        <div class="text-right">
          <b-button type="submit" variant="primary">Submit Song</b-button>
        </div>
      </b-form>
    </ValidationObserver>
  </div>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { email, required } from "vee-validate/dist/rules";
import * as ajax from "@/utilities/ajax";
import config from "@/config";

// Validation Rules
extend("email", email);
extend("required", required);
extend("url", {
  validate(value) {
    if (value) {
      return /^(http:\/\/www\.|https:\/\/www\.|http:\/\/|https:\/\/)?[a-z0-9]+([-.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/.test(
        value
      );
    }

    return false;
  },
  message: "This value must be a valid URL"
});

export default {
  name: "MixingForm",
  components: {
    ValidationObserver,
    ValidationProvider
  },
  data() {
    return {
      fields: {
        artist_name: "",
        email: "",
        name: "",
        song_bpm: "",
        song_files: "",
        song_key: "",
        song_reference: "",
        song_title: ""
      },
      hideDelay: 5000,
      honeypot: "", // Used as a honeypot
      message: {
        failure: "There was an issue, please try again.",
        success: "Your message was received successfully."
      },
      show: {
        failure: false,
        form: true,
        loading: false,
        success: false
      }
    };
  },
  computed: {
    formData() {
      return Object.assign(
        {
          _email: {
            from: this.fields.name,
            replyto: this.fields.email,
            subject: `Mixing Form: ${this.fields.name}`
          },
          _honeypot: this.subject
        },
        this.fields
      );
    }
  },
  methods: {
    closeModal(message) {
      this.show.loading = false;
      this.show[message] = true;

      setTimeout(() => {
        this.$root.$emit("bv::hide::modal", "mixing");
        this.resetForm();
      }, this.hideDelay);
    },
    onSubmit() {
      // Set to Loading
      this.show.form = false;
      this.show.loading = true;

      // Scroll to Top of Form
      this.$smoothScroll({ scrollTo: this.$refs.form, offset: -100 });

      // Ajax Form Submission
      return ajax
        .post(config.forms.mixing, this.formData)
        .then(() => this.closeModal("success"))
        .catch(() => this.closeModal("failure"));
    },
    resetForm() {
      this.fields = {
        email: "",
        message: "",
        name: ""
      };
      this.show = {
        failure: false,
        form: true,
        loading: false,
        success: false
      };
    }
  }
};
</script>
